@import-normalize;
:root {
  --main-color: #212121;
  --gray: #616161;
  --background-color-soft-gray: #f5f4fa;
  --box-sh: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

body {
  font-family: "Trebuchet MS", sans-serif;
  color: var(--main-color);
}
